@import "../../global.scss";
.works {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include mobile {
    justify-content: space-around;
  }
  h1 {
    font-size: 40px;
    @include mobile {
      font-size: 20px;
      overflow: hidden;
    }
  }
  .container {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include mobile {
      flex-direction: column;
      height: 100%;
    }
    .card {
      width: 250px;
      height: 70%;
      border-radius: 10px;
      box-shadow: 0 0 15px -8px black;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 20px;
      transition: all 1s ease;
      @include mobile {
        width: 350px;
        height: 180px;
        margin: 10px 0;
      }
      &.featured {
        width: 300px;
        height: 75%;
        margin: 0 30px;
        @include mobile {
          height: 180px;
          width: 350px;
          margin: 1px;
        }
      }

      &:hover {
        transform: scale(1.1);
      }
      .top {
        display: flex;
        align-items: center;
        justify-content: center;
        .left,
        .right {
          font-size: 30px;
          color: gray;
          transition: all 0.5s ease;
          &:hover {
            color: black;
            font-size: 40px;
            cursor: pointer;
          }
        }
        img {
          &.user {
            height: 60px;
            width: 60px;
            border-radius: 50%;
            object-fit: cover;
            margin: 0 30px;
            transition: all 0.5s ease;
            @include mobile {
              width: 30px;
              height: 30px;
            }
          }
        }
      }
      .center {
        padding: 10px;
        background-color: rgb(235, 235, 235);
        border-radius: 10px;
        font-family: "Noto Sans JP", sans-serif;
      }
      @include mobile {
        font-size: 12px;
        padding: 5px;
      }
      .bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        h3 {
          margin-bottom: 5px;
          @include mobile {
            font-size: 14px;
          }
        }
        h4 {
          color: grey;
          @include mobile {
            font-size: 13px;
          }
        }
      }
    }
  }
}
